<template>
  <div class="login">
    <el-card>
      <div class="active">
        <img src="../assets/logo.png" alt />
      </div>
      <el-form
        :model="loginForm"
        ref="loginRef"
        :rules="loginRul"
        label-width="30%"
        label-position="top"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="loginForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="loginForm.password"></el-input>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="login">登录</el-button>
      <el-button type="info" @click="resetFields">重置</el-button>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginForm: {
        username: "admin",
        password: "123456"
      },
      loginRul: {
        username: [{ required: true, message: "请输入名称", trigger: "blur" }],
        password: [{ required: true, message: "请输入名称", trigger: "blur" }]
      },
      ctx: ""
    };
  },
  methods: {
    resetFields() {
      this.$refs.loginRef.resetFields();
    },
    async login() {
      this.$refs.loginRef.validate(async valid => {
        if (!valid) return;
        let { data: res } = await this.$http.post("login", this.loginForm);
        console.log(res);

        if (res.meta.status != 200) {
          window.sessionStorage.removeItem("token");
          return this.$message.error(res.meta.msg);
        }
        this.$message.success(res.meta.msg);
        window.sessionStorage.setItem("token", res.data.token);
        this.$router.push("/home");
      });
    }
  },
  created: function() {}
};
</script>

<style>
.login {
  background: #2b4b6b;
  height: 100%;
}
.login .el-card {
  width: 100%;
  height: 100%;
  background: #2b4b6b;border: none;
}
.login .el-card__body {
  position: relative;
  width: 490px;
  /* height: 560px; */
  margin: 100px auto;
  border: 1px solid #eee;
  background: #ffffff;
}
.login .shiz {
  background: #000;
}
.login .active {
  position: absolute;
  top: -93px;
  left: 184px;
  width: 130px;
  height: 130px;
  padding: 10px;
  background: #fff;
  border: 1px solid #eeeeee;
  border-radius: 50%;
  box-shadow: 0 0 10px 0;
}
 .login .active img {
  width: 100%;
  height: 100%;
    border-radius: 50%;
}
</style>
